export default (value) => {
  const city = { 11: '', 12: '', 13: '', 14: '', 15: '', 21: '', 22: '', 23: '', 31: '', 32: '', 33: '', 34: '', 35: '', 36: '', 37: '', 41: '', 42: ' ', 43: '', 44: '', 45: '', 46: '', 50: '', 51: '', 52: '', 53: '', 54: ' ', 61: '', 62: '', 63: '', 64: '', 65: '', 71: '', 81: '', 82: '', 91: ' ' }
  if (!city[value.substr(0, 2)]) {
      return false
  }
  const reg = /^\d{6}(18|19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|x|X)$/
  if (!reg.test(value)) {
      return false
  }
  return true
}


