
export default {
  required: (filed) => `${filed}`,
  numberic: (filed) => `${filed}`,
  email: (filed) => `${filed}`,
  date: (filed, format) => `${filed}: ${format || 'YYYY-MM-DD'}`,
  max: (filed, length) => `${filed}${length}`,
  min: (filed, length) => `${filed}${length}`,
  len: (filed, length) => `${filed}${length}`,
  identity: (filed) => `${filed}`
}





